import { Suspense, lazy } from "react";
// eslint-disable-next-line no-unused-vars
import { motion } from "framer-motion";
import { Badge } from "../ui/badge";
import { ArrowLeft, ArrowRight } from "lucide-react";
import { ROUTES } from "@/config/SiteConfig";
import { NavLink } from "react-router-dom";
import { FormPrimaryButton } from "../customComponents/FormButtons";
// Lazy components
const Carousel = lazy(() =>
  import("../ui/carousel").then((m) => ({ default: m.Carousel }))
);
const CarouselContent = lazy(() =>
  import("../ui/carousel").then((m) => ({ default: m.CarouselContent }))
);
const CarouselItem = lazy(() =>
  import("../ui/carousel").then((m) => ({ default: m.CarouselItem }))
);
const CarouselNext = lazy(() =>
  import("../ui/carousel").then((m) => ({ default: m.CarouselNext }))
);
const CarouselPrevious = lazy(() =>
  import("../ui/carousel").then((m) => ({ default: m.CarouselPrevious }))
);
const ImageCard = lazy(() =>
  import("../customComponents/cardTemplates").then((m) => ({
    default: m.ImageCard,
  }))
);

export default function FeaturedDestinationsSection({ id }) {
  const destinations = [
    {
      title: "Blue Hole Diving",
      subtitle: "World Famous Dive Site",
      description:
        "Experience the world's most famous diving spot with crystal clear waters, incredible marine life, and depths that challenge even experienced divers.",
      image: "card2.webp",
      badge: "Must Visit",
      rating: "4.9",
      location: "Dahab Coast",
      price: "$85",
      buttonText: "Book Dive",
    },
    {
      title: "Mount Sinai Trek",
      subtitle: "Sacred Mountain",
      description:
        "Climb the legendary Mount Sinai and witness one of the world's most spectacular sunrises from this historically significant peak.",
      image: "card3.webp",
      badge: "Spiritual Journey",
      rating: "4.8",
      location: "Sinai Peninsula",
      price: "$45",
      buttonText: "Join Trek",
    },
    {
      title: "Colored Canyon",
      subtitle: "Natural Wonder",
      description:
        "Journey through stunning rock formations with vibrant colors created by millions of years of geological processes.",
      image: "card4.webp",
      badge: "Adventure",
      rating: "4.7",
      location: "Nuweiba Road",
      price: "$55",
      buttonText: "Explore Now",
    },
    {
      title: "Three Pools",
      subtitle: "Snorkeling Paradise",
      description:
        "Discover natural rock pools teeming with marine life, perfect for snorkeling and underwater photography.",
      image: "card5.webp",
      badge: "Family Friendly",
      rating: "4.6",
      location: "South Dahab",
      price: "$35",
      buttonText: "Book Tour",
    },
    {
      title: "Abu Galum",
      subtitle: "Bedouin Beach Escape",
      description:
        "Visit the serene Abu Galum Reserve, surrounded by mountains and crystal-clear waters. Enjoy camel rides, snorkeling, and traditional Bedouin hospitality.",
      image: "card1.webp",
      badge: "Eco Adventure",
      rating: "4.7",
      location: "Between Blue Hole and Nuweiba",
      price: "$40",
      buttonText: "Book Tour",
    },
  ];

  return (
    <motion.div
      id={id}
      className="w-full py-16 px-4"
      initial={{ opacity: 0, y: 40 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8, delay: 0.2 }}
      viewport={{ once: true }}
    >
      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-12">
          <Badge className="mb-4 bg-yellow-100 dark:bg-yellow-900/30 text-yellow-800 dark:text-yellow-400 border-yellow-300 dark:border-yellow-700">
            Top Destinations
          </Badge>
          <h2 className="text-3xl sm:text-4xl md:text-5xl font-bold text-foreground mb-4">
            Popular Destinations
          </h2>
          <p className="text-lg sm:text-xl text-muted-foreground max-w-2xl mx-auto">
            Explore the most breathtaking locations that make Dahab a
            world-renowned destination
          </p>
        </div>

        <div className="flex items-center justify-center gap-3 mb-6 text-muted-foreground">
          <ArrowLeft className="w-5 h-5" />
          <h4 className="text-sm font-medium">Swipe</h4>
          <ArrowRight className="w-5 h-5" />
        </div>

        <Suspense fallback={<div className="text-center">Loading...</div>}>
          <Carousel className="w-full">
            <CarouselContent className="-ml-2 md:-ml-4">
              {destinations.map((destination, index) => (
                <CarouselItem
                  key={index}
                  className="pl-2 md:pl-4 md:basis-1/2 lg:basis-1/3"
                >
                  <div className="h-full">
                    <Suspense
                      fallback={<div className="h-64 bg-muted animate-pulse" />}
                    >
                      <ImageCard {...destination} />
                    </Suspense>
                  </div>
                </CarouselItem>
              ))}
            </CarouselContent>
            <div className="hidden md:block">
              <CarouselPrevious className="left-2" />
              <CarouselNext className="right-2" />
            </div>
          </Carousel>
        </Suspense>
      </div>
      <div className="w-full flex justify-center items-center py-16">
        <NavLink to={ROUTES.destinations}>
          <FormPrimaryButton className="px-24 py-6 text-lg sm:text-xl rounded-xl shadow-md">
            View all <ArrowRight />
          </FormPrimaryButton>
        </NavLink>
      </div>
    </motion.div>
  );
}
